//library
import React, { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { SearchOutlined, RollbackOutlined } from "@ant-design/icons";
import styled from "styled-components";
//component
import ProductCard from "../../Components/Product/ProductCard";
import SelectBox from "../../Components/Utility/SelectBox";
import RecentView from "../../Components/Product/RecentView";
// import ProductRank from "../../Components/Product/ProductRank";
import ModalBase from "../../Components/Modal/ModalBase";
import Loading from "../../Components/Utility/Loading";
import Switch from "../../Components/Utility/Switch";
import ProductRankCarousel from "./ProductRankCarousel";

//comstom hooks
import useAxios from "../../hooks/useAxios";
import useModal from "../../hooks/useModal.js";

//etc
import { categoryList, priceList } from "../../data/CatecoryList";

function Main() {
  const [readRef, setReadRef] = useInView();

  const [click, setClick] = useState(true); //   or 
  const [searchValue, setSearchValue] = useState(""); //
  const [searchTrue, setSearchTrue] = useState(false); // 
  const [selectCategory, setSelectCategort] = useState(""); // 
  const [priceRange, setPriceRange] = useState(); //  
  const [skip, setSkip] = useState(0); //   
  const limit = 8; //   

  //  
  const {
    resData: productList,
    loading,
    connectServer: getProduct,
    lastData,
    setLastData,
  } = useAxios("api/product/productList");

  //
  const { openModal, contents, setOpenModal, setContents } = useModal();

  //       
  useEffect(() => {
    const getMainView = JSON.parse(localStorage.getItem("mainView"));
    if (getMainView !== null) {
      setClick(getMainView);
    }

    const titleName = document.getElementsByTagName("title")[0];
    titleName.innerHTML = ` `;
  }, []);

  //   
  const view = useCallback(() => {
    if (!click) {
      localStorage.setItem("mainView", true);
      setClick(true);
    }

    if (click) {
      localStorage.setItem("mainView", false);
      setClick(false);
    }
  }, [click]);

  //  or ,    
  useEffect(() => {
    setSkip(0);
    const onCategorySearch = () => {
      const option = {
        limit: limit,
        skip: 0,
        category: selectCategory,
        price: priceRange,
        readMore: false,
        searchValue: searchValue,
      };
      getProduct(option);
    };

    onCategorySearch();
  }, [selectCategory, priceRange]);

  //   
  useEffect(() => {
    const readMore = () => {
      const option = {
        limit: limit,
        skip: skip + limit,
        category: selectCategory,
        price: priceRange,
        searchValue: searchValue,
        readMore: true,
      };
      getProduct(option);
      setSkip((prev) => prev + limit);
    };

    if (setReadRef) {
      readMore();
    }
  }, [setReadRef]);

  //  
  const onKeywordSearch = (e) => {
    e.preventDefault();
    if (searchValue.length === 0) {
      setContents({
        title: " ",
        message: " .",
      });
      setOpenModal(true);
    }

    if (searchValue.length > 0) {
      const option = {
        limit: limit,
        skip: 0,
        category: selectCategory,
        price: priceRange,
        readMore: false,
        searchValue: searchValue,
      };
      setSearchTrue(true);
      getProduct(option);
      setSkip(0);
    }
  };

  const onSearchValue = (e) => {
    setSearchValue(e.target.value);
  };

  const onSearchReset = () => {
    setSearchValue("");
    setSkip(0);
    setLastData(false);
    setSearchTrue(false);
    getProduct({
      limit: limit,
      skip: 0,
      category: selectCategory,
      price: priceRange,
      readMore: false,
      searchValue: "",
    });
  };

  return (
    <div className="page">
      <RecentView page={true} />

      <ModalBase
        contents={contents}
        modalOpen={openModal}
        setModalOpen={setOpenModal}
      />

      <MainOption>
        <div>
          <SelectBox
            data={categoryList}
            setData={setSelectCategort}
            main={true}
          />
          <SelectBox data={priceList} setData={setPriceRange} main={true} />
        </div>

        <Switch viewType={click} onSetMode={view} />
      </MainOption>

      <MainSearchBar onSubmit={onKeywordSearch}>
        <input
          value={searchValue}
          onChange={onSearchValue}
          placeholder=" ."
        />
        <div>
          <SearchOutlined onClick={onKeywordSearch} />
        </div>
      </MainSearchBar>

      {searchTrue ? (
        <SearchReset>
          <div onClick={onSearchReset}>
            <RollbackOutlined />
          </div>
        </SearchReset>
      ) : (
        // <ProductRank />
        <ProductRankCarousel />
      )}

      {loading ? (
        <Loading />
      ) : (
        <>
          <ProductCard data={productList} viewType={click} />
          {!lastData && <ReadMore ref={readRef} />}
        </>
      )}
    </div>
  );
}

const MainOption = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;

  & > :first-child {
    display: flex;

    @media (max-width: 380px) {
      flex-direction: column;
    }
  }
`;

const MainSearchBar = styled.form`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all ease 0.5s;

  input {
    background-color: var(--gray_transparency);
    border: 2px solid var(--orange_normal);
    border-radius: 5px;
    width: 300px;
    margin: 1rem;
    padding: 5px;
    outline: none;

    &:focus {
      border: 2px solid var(--orange_hover);
    }
  }

  div {
    font-size: 20px;
    cursor: pointer;
  }
`;

const SearchReset = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;

  div {
    font-size: 2rem;
    padding: 0.3rem;
    border-radius: 10px;

    &:hover {
      cursor: pointer;
      background-color: var(--orange_hover);
    }
  }
`;

const ReadMore = styled.div`
  width: 100%;
  height: 10px;
`;

export default React.memo(Main);
