import React, { useEffect, useState } from "react";
import Dropzone from "react-dropzone";
import axios from "axios";
import styled from "styled-components";
import { CameraOutlined, CloseOutlined } from "@ant-design/icons";

import { postUrl } from "../../PostUrl";

function ImgUpload({ setState, setImgDelete, edit, editImg }) {
  const [img, setImg] = useState([]);

  useEffect(() => {
    if (edit) {
      setImg(editImg.image);
    }
  }, []);

  useEffect(() => {
    setState([...img]);
  }, [img]);

  const dropItem = (files) => {
    if (img.length === 12) {
      return alert("  12 .");
    }

    const formData = new FormData();
    const config = {
      header: { "content-type": "multipart/form-data" },
    }; //header    

    formData.append("file", files[0]);
    //formData     

    sendServerImageData(formData, config);
  };

  const sendServerImageData = async (formData, config) => {
    const res = await axios.post("/api/product/img", formData, config);
    const getImg = new Image();
    getImg.onload = function () {
      setImg([
        ...img,
        {
          name: res.data.file.filename,
          width: this.width,
          height: this.height,
          // path: `http://localhost:3001/uploads/${res.data.file.filename}`,
        },
      ]);
    };
    getImg.src = `${postUrl}${res.data.file.filename}`;
  };

  const delImg = async (e) => {
    const copyImg = [...img];
    setImg(copyImg.filter((data) => data !== e));

    //edit        
    if (edit) {
      setImgDelete((data) => [...data, e.name]);
    }

    //     
    if (edit === undefined) {
      const res = await axios.post("/api/product/delImg", { image: e.name });
      if (res.data.success) {
        // alert(" ");
      }

      if (res.data.success === false) {
        alert(" ");
      }
    }
  };

  return (
    <Section>
      <DropBox>
        <Dropzone onDrop={dropItem}>
          {({ getRootProps, getInputProps }) => (
            <div {...getRootProps()}>
              <input {...getInputProps()} />
              <DropzoneIcon>
                <div>
                  <CameraOutlined />
                </div>
                <div> </div>
              </DropzoneIcon>
            </div>
          )}
        </Dropzone>
      </DropBox>

      {img.length > 0 && (
        <>
          {img.map((data, index) => (
            <UploadImg
              img={`url('${postUrl}${data.name}')`}
              key={index}
              alt="img"
            >
              <UploadImgDel onClick={() => delImg(data)}>
                <CloseOutlined />
              </UploadImgDel>
            </UploadImg>
          ))}
        </>
      )}
    </Section>
  );
}

const Section = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-height: 20rem;
  overflow-y: auto;
`;

const DropBox = styled.div`
  background-color: transparent;
  width: 10rem;
  height: 10rem;
  border-radius: 5px;
  border: 2px solid var(--gray);
  margin: 0.3rem;

  & > :first-child {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    border-radius: 5px;

    &:hover {
      cursor: pointer;
      background-color: var(--gray_transparency);
    }
  }

  @media (max-width: 550px) {
    width: 6rem;
    height: 6rem;
  }
`;

const DropzoneIcon = styled.div`
  & > :first-child {
    display: flex;
    justify-content: center;
    font-size: 2.5rem;
  }

  & > :nth-child(2) {
    font-size: 1rem;
  }
`;

const UploadImg = styled.div`
  background-image: ${(props) => props.img};
  width: 10rem;
  height: 10rem;
  border: 2px solid rgba(146, 146, 146, 0.9);
  border-radius: 5px;

  margin: 0.3rem;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  position: relative;

  @media (max-width: 550px) {
    width: 6rem;
    height: 6rem;
  }
`;

const UploadImgDel = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;

  position: absolute;
  z-index: 10;
  top: 5px;
  right: 5px;

  background-color: rgba(184, 184, 184, 0.5);
  width: 1.8rem;
  height: 1.8rem;
  font-size: 1.5rem;
  border-radius: 5px;

  cursor: pointer;
`;

export default React.memo(ImgUpload);
